import React from 'react'

export default function About() {
    return (
        <div>
            <div className="row">
              <div className="" id="board">
    <div className="col s12 m6  left"><h2>About Joy</h2>
      <div className="card large  black ">
        <div className="card-content white-text">
         
          <p className="joy">Joy Chiadika is a climate change advocate with keen interest in building climate resilient communities in Nigeria and around Africa. With experience leading projects in Nigeria, Joy supports organisation to implement capacity building programmes to tackle energy poverty, climate change and environmental sustainability.

A graduate of Delta State University and the Lagos Business School on Sustainability, Joy is presently the Team Lead of Lagos State for REES Africa and is keen on building a sustainable Africa for youth and driven by youth.

When not working, Joy Chiadika is actively volunteering with humanitarian focused organisations and reading. </p>
        </div>
      </div>
    </div>
  </div>
  </div>

        </div>
    )
}
