import React, { Component } from "react";
import picture from "./picture.jpg";
import four from "./four.PNG";
import speak from "./speak.PNG";
import sus from "./sus.PNG";
import web from "./web.PNG";
import { Link } from "react-router-dom";

export default class Index extends Component {

  render() {
    return (
      <div class="col s12 m8 offset-m2 l6 ">
        <div class="card-panel white lighten-2 z-depth-4">
          <div class="row valign-wrapper  ml-5 mr-5 mb-5">
            <div class="col s8 ">
              <img src={picture} alt="background" class="responsive-img" />
            </div>
            <div class="col s12">
              <h3 class="banner-text ">
                <small>Hello, I’m</small>
                <br></br>
                Joy Chiadika
              </h3>
              <p className="p">Techie, and sustainability expert</p>
              <br></br>
              <Link to="/contact" class="btn btn-large black  ">
                Hire me
              </Link>
            </div>
          </div>
        </div>

        <h4 className="boo center mt-5" id="service">
          My Services
        </h4>

        {/* div for cards */}

        <div className="card-margin">
          <div className="row  ">
            <div className="col s6 m6 l3">
              <div className="card">
                <div className="card-image">
                  <img src={web} alt="" className="img-responsive" />
                 
                </div>
                <div class="card-content">
                <span className="card-title" id="darc">
                   
                    Full Web Development
                  </span>
                  <p>
                    Are you looking to create a web application? We've got the
                    most affordable prices with the most satisfied clients.
                  </p>
                </div>
                <div class="card-action">
                  <Link to="/contact">Check out my projects here!</Link>
                </div>
              </div>
            </div>

            <div className="col s6 m6 l3">
              <div className="card">
                <div className="card-image">
                  <img src={sus} alt="" className="img-responsive" />
                  
                </div>
                <div class="card-content">
                <span className="card-title" id="darc">
                    Environmental Development
                  </span>
                  <p>
                    Over the past 3 years, I have worked 15 environmental
                    sustainability and developmental projects. Let's partner!
                  </p>
                </div>
                <div class="card-action">
                  <a to="/contact">Let's partner</a>
                </div>
              </div>
            </div>
            <div className="card-margin">
              <div className="row  ">
                <div className="col s6 m6 l3">
                  <div className="card">
                    <div className="card-image">
                      <img src={speak} alt="" className="img-responsive" />
                     
                    </div>
                    <div class="card-content">
                    <span className="card-title" id="darc">
                        Youth Trainer
                      </span>
                      <p>
                        Are you searching for a moderator for your event? A
                        youth trainer and specialist? Connect, Let's dicuss.
                      </p>
                    </div>
                    <div class="card-action">
                      <a to="#">I am at your service!</a>
                    </div>
                  </div>
                </div>

                <div className="col s6 m6 l3">
                  <div className="card">
                    <div className="card-image">
                      <img src={four} alt="" className="img-responsive" />
                    
                    </div>
                    <div class="card-content">
                    <span className="card-title" id="darc">
                        Sustainabilty Expert
                      </span>
                      <p>
                        Contact me for your projects sustainability related
                        issues as well as speaking engagements on
                        sustainability.
                      </p>
                    </div>
                    <div class="card-action">
                      <a to="#">Contact me now</a>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}
